import React, { useState, useEffect } from "react"
import { Section, ButtonLink, Empty } from "components"
import { Form, Button, Row, Col, message, Popconfirm, Tabs, Divider } from "antd"
import * as yup from "yup"
import uuid from "uuid/v4"
import DataList from "./DataList"
import moment from "moment"
import { useMutation } from "@apollo/react-hooks"
import {
	mutateUpdateExperiences,
	UPDATE_EXPERIENCES_NON_ACADEMIC,
	DELETE_EXPERIENCES_NON_ACADEMIC,
	DELETE_EXPERIENCES,
	DELETE_EXPERIENCES_LEVEL
} from "queries/profile"
import { newClient, mobile, renderError } from "helpers"
import SelectInput from "components/forms/SelectInput"
import { Formik } from "formik"
import TextInput from "components/forms/TextInput"
import { DatePicker, SubmitButton } from "formik-antd"
import CheckboxInput from "components/forms/CheckboxInput"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { useProgrammeList } from "helpers/hooks/data"
import Loading from "components/Loading"

function Experiences({ onEditMode, onExperiences, unauthorizedUser, i18n, userData, ...props }) {
	const { fetchUser, loading } = props
	const { editMode, setEditMode } = onEditMode
	const { experiences, experiencesNonAcademic } = onExperiences
	const [userExperiences, setUserExperiences] = useState([])
	const [userExperiencesNonAcademic, setUserExperiencesNonAcademic] = useState([])
	const [deletedLevels, setDeletedLevels] = useState([])
	const [isAdding, setIsAdding] = useState(false)
	const [activeKey, setActiveKey] = useState("academic")
	const { options } = useProgrammeList({ isNonAcademic: activeKey === "non-academic", useLabelForValue: true })

	const [updateExperiences, { loading: loadingUpdate }] = useMutation(mutateUpdateExperiences, {
		client: newClient(),
		onCompleted: () => {
			fetchUser()
				.then(() => message.success(i18n._(t`The experience item has been updated`), 2))
				.finally(() => {
					setIsAdding(false)
					setEditMode("")
				})
		},
		onError: (err) => renderError(err)
	})

	const [updateNonAcademic] = useMutation(UPDATE_EXPERIENCES_NON_ACADEMIC, {
		client: newClient(),
		onCompleted: () => {
			fetchUser()
			setIsAdding(false)
			setEditMode("")
		},
		onError: (err) => renderError(err)
	})

	const [deleteExperiences] = useMutation(DELETE_EXPERIENCES, {
		client: newClient(),
		onCompleted: () => {
			setEditMode("")
			setIsAdding(false)
			fetchUser().then(() => message.success(i18n._(t`The experience item has been deleted`), 2))
		},
		onError: (err) => renderError(err)
	})

	const [deleteLevel] = useMutation(DELETE_EXPERIENCES_LEVEL, {
		client: newClient(),
		onCompleted: () => setDeletedLevels([]),
		onError: (err) => renderError(err)
	})

	const [deleteNonAcademic] = useMutation(DELETE_EXPERIENCES_NON_ACADEMIC, {
		client: newClient(),
		onCompleted: () => {
			setEditMode("")
			setIsAdding(false)
			fetchUser().then(() => message.success(i18n._(t`The experience item has been deleted`), 2))
		},
		onError: (err) => renderError(err)
	})

	const details = (item) => {
		const { subject = {}, isPrivateTutor } = item
		const level = (subject.level && subject.level[0]) || {}
		const monthFrom = level.fromDate?.split("-")[1]
		const yearFrom = level.fromDate?.split("-")[0]
		const monthTo = level.toDate?.split("-")[1]
		const yearTo = level.toDate?.split("-")[0]
		const totalExpMonths = moment(`${yearTo} ${monthTo}`).diff(moment(`${yearFrom} ${monthFrom}`), "months")
		const totalExp = Number((totalExpMonths / 12).toFixed(1))

		return (
			<section>
				{subject.level?.map((l, idx) => (
					<p key={idx}>
						{l.curriculum}{" "}
						<span style={{ color: "#999" }}>{isPrivateTutor ? <Trans>Private tutor</Trans> : l.institution}</span>
					</p>
				))}
				<p style={{ marginBottom: 0 }}>
					{moment(monthFrom).format("MMM")} {yearFrom} - {moment(monthTo).format("MMM")} {yearTo} &middot;{" "}
					<span style={{ color: "#999" }}>
						<Trans>{totalExp}y experience</Trans>
					</span>
				</p>
			</section>
		)
	}

	const handleAddNewField = () => {
		setIsAdding(true)
		setUserExperiences((exps) => [
			...exps,
			{
				id: uuid(),
				isPrivateTutor: false,
				isPresent: false,
				institution: "",
				level: <Trans>Select your level</Trans>,
				subject: <Trans>Select your subject</Trans>,
				fromDate: "2019-01",
				toDate: "2019-01"
			}
		])
	}

	const handleSave = (values, { setSubmitting }) => {
		const hasPvid = values.hasOwnProperty("pvid")
		const fromDate = moment(values.fromDate).format("YYYY-MM-DD")
		const toDate = values.isPresent ? moment().format("YYYY-MM-DD") : moment(values.toDate).format("YYYY-MM-DD")
		const institution = values.isPrivateTutor ? "-" : values.institution || "-"

		if (activeKey === "non-academic") {
			values = {
				subject: Array.isArray(values.subject) ? values.subject.join(" > ") : values.subject,
				isNonAcademic: true,
				isStore: !hasPvid,
				level: [
					{
						pvid: values.pvid || "",
						fromDate,
						toDate,
						institution,
						isPrivateTutor: values.isPrivateTutor,
						isPresent: values.isPresent,
						curriculum: ""
					}
				]
			}
		} else {
			const { subject, initialSubject, ...rest } = values
			const level = values.curriculum.map((curriculum) => {
				const index = userExperiences.findIndex((item) => item.subject.name === values.initialSubject)
				const selectedLevel = userExperiences[index]?.subject?.level?.find((item) => item.curriculum === curriculum) || {}

				return {
					...rest,
					pvid: selectedLevel.pvid || "",
					curriculum,
					fromDate,
					toDate,
					institution
				}
			})
			values = { subject: values.subject, isNonAcademic: false, isStore: !hasPvid, level }
		}

		if (hasPvid) {
			const { source, ...rest } = values
			values = rest
		}

		if (deletedLevels.length > 0) {
			const pvidList = deletedLevels.map((item) => item.pvid).filter((item) => Boolean(item))
			deleteLevel({ variables: { pvidList } })
		}

		const handler = activeKey === "non-academic" ? updateNonAcademic : updateExperiences

		handler({ variables: { list: values } }).finally(() => setSubmitting(false))
	}

	const handleDeleteLocalLevels = (subject) => (level) => {
		const index = userExperiences.findIndex((item) => item.subject.name === subject)
		const selected = userExperiences[index].subject?.level?.find((item) => item.curriculum === level) || {}
		setDeletedLevels((prev) => [...prev, selected])
	}

	const handleCancel = () => {
		setUserExperiences(experiences)
		setEditMode("")
		setIsAdding(false)
	}

	const handleDelete = (subject) => {
		const handler = activeKey === "non-academic" ? deleteNonAcademic : deleteExperiences
		handler({ variables: { subject } })
	}

	useEffect(() => {
		setUserExperiences(experiences)
		setUserExperiencesNonAcademic(experiencesNonAcademic)
	}, [experiences, experiencesNonAcademic])

	return (
		<Section paddingHorizontal={0} noPadding={mobile}>
			{editMode === "experiences" ? (
				<EditMode
					i18n={i18n}
					isAdding={isAdding}
					loading={loadingUpdate}
					data={{ userExperiences, userExperiencesNonAcademic, activeKey, options }}
					handlers={{ handleAddNewField, handleCancel, handleDelete, handleSave, setEditMode, handleDeleteLocalLevels }}
				/>
			) : (
				<Tabs onChange={(key) => setActiveKey(key)} defaultActiveKey={activeKey}>
					<Tabs.TabPane key="academic" tab="Academic">
						{userExperiences.length === 0 && !loading && (
							<Empty
								description={
									unauthorizedUser ? (
										<Trans>This tutor didn't set any tutoring experiences yet</Trans>
									) : (
										<Trans>You haven't set any tutoring experiences yet</Trans>
									)
								}
							/>
						)}

						{userExperiences.length > 0 &&
							userExperiences.map((item, idx) => {
								const { subject } = item
								return <DataList key={idx} loading={loading} title={`${subject.name}`} description={details(item)} />
							})}
					</Tabs.TabPane>
					<Tabs.TabPane key="non-academic" tab="Non-academic">
						{userExperiencesNonAcademic.length === 0 && !loading && (
							<Empty
								description={
									unauthorizedUser ? (
										<Trans>This tutor didn't set any tutoring experiences yet</Trans>
									) : (
										<Trans>You haven't set any tutoring experiences yet</Trans>
									)
								}
							/>
						)}

						{userExperiencesNonAcademic.length > 0 &&
							userExperiencesNonAcademic.map((item, idx) => {
								const { subject } = item
								return <DataList key={idx} loading={loading} title={`${subject.name}`} description={details(item)} />
							})}
					</Tabs.TabPane>
				</Tabs>
			)}
		</Section>
	)
}

function EditMode({ isAdding, i18n, data, handlers, loading }) {
	const { handleSave, handleCancel, handleDelete, handleAddNewField, setEditMode, handleDeleteLocalLevels } = handlers
	const { userExperiences, userExperiencesNonAcademic: nonAcademic, activeKey, options } = data
	const experiences = activeKey === "non-academic" ? nonAcademic : userExperiences

	if (loading) return <Loading />

	if (isAdding) {
		return (
			<Formik
				onSubmit={handleSave}
				validationSchema={activeKey === "non-academic" ? null : experiencesSchema(i18n)}
				initialValues={{ isPresent: false, isPrivateTutor: false }}
			>
				{({ handleSubmit, values }) => (
					<Form layout="vertical" onSubmit={handleSubmit}>
						<Row gutter={16} type="flex">
							<Col lg={activeKey === "non-academic" ? 16 : 8} xs={24}>
								<TextInput
									name="institution"
									placeholder={i18n._(t`Tuition's name`)}
									label={<Trans>Tuition's name</Trans>}
									disabled={values.isPrivateTutor}
								/>
							</Col>
							<Col lg={8} xs={12}>
								<SelectInput
									cascader={activeKey === "non-academic"}
									name="subject"
									label={<Trans>Subject</Trans>}
									placeholder={i18n._(t`Subject`)}
									options={options.subjects}
								/>
							</Col>
							{activeKey === "academic" && (
								<Col lg={8} xs={12}>
									<SelectInput
										name="curriculum"
										mode="multiple"
										label={<Trans>Curriculum</Trans>}
										placeholder={i18n._(t`Curriculum`)}
										options={options.levels}
									/>
								</Col>
							)}
						</Row>
						<Row gutter={16} type="flex" align="middle" style={{ marginBottom: "2em" }}>
							<Col lg={6} xs={12} className="mb2em-mobile">
								<DatePicker.MonthPicker
									name="fromDate"
									label={<Trans>Date joined</Trans>}
									placeholder={i18n._(t`Date joined`)}
									style={{ width: "100%" }}
									disabledDate={(current) => current && current > moment()}
								/>
							</Col>
							<Col lg={6} xs={12} className="mb2em-mobile">
								<DatePicker.MonthPicker
									name="toDate"
									label={<Trans>Date resigned</Trans>}
									placeholder={i18n._(t`Date resigned`)}
									style={{ width: "100%" }}
									disabledDate={(current) => current && current > moment()}
									disabled={values.isPresent}
								/>
							</Col>
							<Col lg={6} xs={24}>
								<CheckboxInput name="isPrivateTutor" className="mb0">
									<Trans>I was a private tutor</Trans>
								</CheckboxInput>
							</Col>
							<Col lg={6} xs={24}>
								<CheckboxInput name="isPresent" className="mb0">
									<Trans>I'm currently working here</Trans>
								</CheckboxInput>
							</Col>
						</Row>
						<SubmitButton block>
							<Trans>Save this experience</Trans>
						</SubmitButton>{" "}
						&nbsp;{" "}
						<ButtonLink block onClick={handleCancel}>
							<Trans>Cancel</Trans>
						</ButtonLink>
					</Form>
				)}
			</Formik>
		)
	}

	return (
		<>
			{(experiences || []).map(({ subject = {} }, idx) => {
				const level = subject.level && subject.level[0]
				const getSubjectDefault = () => {
					const subjectName = experiences[idx]?.subject.name.split(" > ") || ""
					const theSubject = options.subjects?.find((item) => item.label === subjectName[0]) || {}
					const subSubject = theSubject?.children?.find((item) => item.label === subjectName[1]) || {}
					return activeKey === "non-academic" ? [theSubject.label, subSubject.label] : subject.name
				}
				return (
					<Formik
						enableReinitialize
						key={idx}
						onSubmit={handleSave}
						initialValues={{
							pvid: level.pvid || "",
							subject: getSubjectDefault(),
							initialSubject: getSubjectDefault(),
							institution: level.institution,
							fromDate: level.fromDate,
							toDate: level.toDate,
							isPresent: level.isPresent,
							isPrivateTutor: level.isPrivateTutor,
							curriculum: (subject.level || []).map((item) => item.curriculum)
						}}
					>
						{({ handleSubmit, values }) => (
							<Form layout="vertical" onSubmit={handleSubmit} className="mb2em">
								<Row gutter={16}>
									<Col lg={8}>
										<TextInput
											name="institution"
											label={<Trans>Tuition's name</Trans>}
											placeholder={i18n._(t`Tuition's name`)}
											disabled={values.isPrivateTutor}
										/>
									</Col>
									<Col lg={8}>
										<SelectInput
											cascader={activeKey === "non-academic"}
											name="subject"
											label={<Trans>Subject</Trans>}
											placeholder={i18n._(t`Subject`)}
											options={options.subjects}
										/>
									</Col>
									{activeKey === "academic" && (
										<Col lg={8}>
											<SelectInput
												name="curriculum"
												mode="multiple"
												label={<Trans>Curriculum</Trans>}
												placeholder={i18n._(t`Curriculum`)}
												options={options.levels}
												onDeselect={handleDeleteLocalLevels(subject.name)}
											/>
										</Col>
									)}
								</Row>
								<Row gutter={16} type="flex" align="middle" style={{ marginBottom: "2em" }}>
									<Col lg={6} xs={12} className="mb2em-mobile">
										<DatePicker.MonthPicker
											name="fromDate"
											label={<Trans>Date joined</Trans>}
											placeholder={i18n._(t`Date joined`)}
											style={{ width: "100%" }}
											disabledDate={(current) => current && current > moment()}
										/>
									</Col>
									<Col lg={6} xs={12} className="mb2em-mobile">
										<DatePicker.MonthPicker
											name="toDate"
											label={<Trans>Date resigned</Trans>}
											placeholder={i18n._(t`Date resigned`)}
											disabled={values.isPresent}
											style={{ width: "100%" }}
											disabledDate={(current) => current && current > moment()}
										/>
									</Col>
									<Col lg={6} xs={24}>
										<CheckboxInput name="isPrivateTutor" className="mb0">
											<Trans>I was a private tutor</Trans>
										</CheckboxInput>
									</Col>
									<Col lg={6} xs={24}>
										<CheckboxInput name="isPresent" className="mb0">
											<Trans>I'm currently working here</Trans>
										</CheckboxInput>
									</Col>
								</Row>
								<SubmitButton type="primary">
									<Trans>Update</Trans>
								</SubmitButton>{" "}
								&nbsp;{" "}
								<Popconfirm title={<Trans>Delete this experience?</Trans>} onConfirm={() => handleDelete(subject.name)}>
									<ButtonLink icon="close">
										&nbsp; <Trans>Delete</Trans>
									</ButtonLink>
								</Popconfirm>
								<Divider />
							</Form>
						)}
					</Formik>
				)
			})}
			<Button block type="dashed" size="medium" icon="plus" onClick={handleAddNewField} style={{ marginBottom: "2em" }}>
				&nbsp; <Trans>Add a new experience</Trans>
			</Button>
			<ButtonLink block icon="close" onClick={() => setEditMode(false)}>
				&nbsp; <Trans>Cancel</Trans>
			</ButtonLink>
		</>
	)
}

const experiencesSchema = (i18n) =>
	yup.object().shape({
		curriculum: yup.string().required(i18n._(t`You have to fill in your curriculum`)),
		subject: yup.string().required(i18n._(t`You have to fill in your subject`))
	})

export default withI18n()(Experiences)
